.container{
    padding: 2rem;
}

.header{
    background-color:#ddd;
}

.row{
    padding: 1rem;
    min-height: 5rem;
}    

.row a {
    text-decoration: underline;
}

@media only screen and (max-width: 576px){
    .header{
        background-color: rgb(255, 238, 0);
        transform: translateY(-100vh);
    }
    

}